<script>
import { Toast } from "mint-ui";
export default {
  name: "btnhidden",
  methods: {
    // 
    setfontsize() {
      var size = this.$store.state.fontsize;
      if (size == "small") {
        this.$store.commit("setshowindex", 0);
        this.$store.commit("setfontsize", "normal");
        Toast("");
      } else if (size === "normal") {
        this.$store.commit("setshowindex", 0);
        this.$store.commit("setfontsize", "big");
        Toast("");
      } else if (size === "big") {
        this.$store.commit("setshowindex", 0);
        this.$store.commit("setfontsize", "small");
        Toast("");
      }
    },
    // 
    golastchapter() {
      var lastindex = this.$store.state.chapterindex - 1;
      if (lastindex < 0) {
        Toast("");
      } else {
        this.$store.commit("setshowindex", 0);
        this.$store.commit("setchapterindex", lastindex);
        this.$router.push({ path: "/reader1" });
      }
    },
    //  
    gonextchapter() {
      if (
        this.$store.state.chapterindex ===
          this.$store.getters.chapterlistnum - 1 &&
        index === this.$store.state.maxindex
      ) {
        Toast("");
      } else {
        var nextindex = this.$store.state.chapterindex + 1;
        this.$store.commit("setchapterindex", nextindex);
        this.$store.commit("setshowindex", 0);
        // this.$store.commit("setlastmaxindex", this.getmaxindex - 1);
        this.$router.push({ path: "/reader1" });
      }
    },
    // 
    url2menu() {
      this.$router.push({ path: "/chapterlist" });
    }
  },

  computed: {
    readpercentage() {
      return (
        (this.$store.state.chapterindex / this.$store.getters.chapterlistnum) *
        100
      );
    },
    menu() {
      return this.$store.state.reader.menushow;
    }
  }
};
</script>

<style>
.fixed-bottom .row {
  text-align: center;
  margin: 0;
}

/*   */
.hiddenbottom {
  background-color: #f8f9fa;
  border-top: 1px solid;
  border-top-color: #c3c3c357;
}

.hiddenbottom .line2 {
  height: 3rem;
}
.hiddenbottom .line2 .col {
  line-height: 32px;
}
.hiddenbottom .line2 i {
  font-size: 1.75rem;
}
.hiddenbottom .line2 p {
  line-height: 0px;
}
/*   */
</style>


