<template>
  <div class="Readermain">
    <!--   -->
    <tophidden></tophidden>
    <!--   -->

    <!--   -->
    <top :lname="chapter['Lname']"></top>
    <!--   -->

    <btnhidden></btnhidden>
    <!--   -->
    <btn></btn>
    <!--   -->

<!--    <mt-popup v-model="popupVisible" position="right" modal="true" style="width:100%;height:100%">-->
<!--      hahahh-->
<!--      <div style="height:30px;width:30px;background-color:black" @click="popupVisible=false"></div>-->
<!--      <br />-->
<!--      <br />aahh-->
<!--    </mt-popup>-->

    <div class="content row">
      <readerview :middleibook="middleibook"></readerview>
      <div class="contentbuts row" :style="{height:MidHeight+ 'px'}">
        <div class="col-3" @click="pagepre"></div>
        <div class="col-6" @click="menushowcontrol"></div>
        <div class="col-3" @click="pagenext"></div>
      </div>
    </div>
    <Download></Download>
  </div>
</template>

<script>
import { Indicator, Toast, Popup, Progress } from "mint-ui";
Vue.component(Popup.name, Popup);
Vue.component(Progress.name, Progress);
import btn from "./reader/btn";
import tophidden from "./reader/tophidden";
import top from "./reader/top";
import readerview from "./reader/readerview";
import btnhidden from "./reader/btnhidden";

import qs from "qs";
import Vue from "vue";
import Download from "./Download";

export default {
  name: "Reader",
  components: {
    btn: btn,
    tophidden: tophidden,
    top: top,
    Download: Download,
    readerview: readerview,
    btnhidden: btnhidden
  },
  data() {
    return {
      chapterurl: this.$route.query.chapterurl,
      contentrouter: true,
      getClientHeight: window.screen.availHeight,
      popupVisible: false,
      getmaxindex: undefined,
      middleibook: undefined
    };
  },
  methods: {
    menushowcontrol() {
      this.$store.commit("changemenustatus");
    },

    // chapterurl    - 
    getchapter(Lurl) {
      try {
        var request = window.indexedDB.open("bookdb", 1);
        request.onsuccess = event => {
          var db = event.target.result;
          var transaction = db.transaction(["chapterlist"], "readwrite");
          var objectStore = transaction.objectStore("chapterlist");
          var index = objectStore.index("chapterurl");

          index.get(Lurl).onsuccess = event => {
            if (event.target.result === undefined) {
              let user = {
                Lurl: Lurl
              };
              this.$api.chapter(user)
                .then(response => {
                  var responseresult = response.data;
                  // this.ibook(responseresult);
                  this.middleibook = responseresult;
                  var request = window.indexedDB.open("bookdb", 1);
                  request.onsuccess = event => {
                    var db = event.target.result;
                    var transaction = db.transaction(
                      "chapterlist",
                      "readwrite"
                    );
                    var objectStore = transaction.objectStore("chapterlist");
                    //   'url'
                    objectStore.add({
                      chapterurl: Lurl,
                      content: responseresult
                    });
                  };
                })
                .catch(function(error) {
                  console.log(error);
                });
            }
            if (event.target.result !== undefined) {
              // this.ibook(event.target.result.content);
              this.middleibook = event.target.result.content;
            }
          };
        };
      } catch (err) {
        console.log(err);
      }
    },

    // 
    pagenext() {
      console.log(this.$store.getters.chapterlistnum, "length");
      var index = this.$store.state.showindex + 1;
      if (
        this.$store.state.chapterindex ===
          this.$store.getters.chapterlistnum - 1 &&
        index === this.$store.state.maxindex
      ) {
        Toast("");
      } else if (index > this.$store.state.maxindex - 1) {
        this.$store.commit("setshowindex", 0);
        var nextindex = this.$store.state.chapterindex + 1;
        this.$store.commit("setchapterindex", nextindex);
        this.$store.commit("setshowindex", 0);
        // this.$store.commit("setlastmaxindex", this.getmaxindex - 1);
        this.$router.push({ path: "/reader1" });
      } else {
        this.$store.commit("setshowindex", index);
      }
    },
    // 
    pagepre() {
      if (
        this.$store.state.chapterindex === 0 &&
        this.$store.state.showindex === 0
      ) {
        Toast("");
      } else if (this.$store.state.showindex === 0) {
        this.$store.commit("setshowindex", 0);
        var lastindex = this.$store.state.chapterindex - 1;
        if (lastindex < 0) {
          Toast("");
        } else {
          this.$store.commit("setshowindex", 0);
          this.$store.commit("setchapterindex", lastindex);
          this.$router.push({ path: "/reader1" });
        }
      } else {
        var index = this.$store.state.showindex - 1;
        this.$store.commit("setshowindex", index);
      }
    }
  },
  computed: {
    chapter() {
      var chapterlists = eval(this.$store.state.chapterlists);
      var chapterindex = this.$store.state.chapterindex;
      var Lname = chapterlists[chapterindex]["Lname"];
      var getstyle = this.$store.state.fontsize;
      var Lurl = chapterlists[chapterindex]["Lurl"];
      this.getchapter(Lurl);
      return { Lname: Lname, Lurl: Lurl };
    },
    // 
    MidHeight() {
      var TotalHeight = window.screen.availHeight;
      return TotalHeight - 26.4 - 72;
    }
  },

  mounted() {
    // 
    if (this.$store.state.nowurl !== "reader") {
      this.$store.commit("updaterouter", "reader");
    }
  },
  beforeCreate() {
    Indicator.open("...");
  },
  // created() {
  //   setTimeout(() => Indicator.close(), 0)
  // },
  updated() {
    setTimeout(() => Indicator.close(), 0)
  },
  beforeUpdate() {
    Indicator.open("...");
  }
};
</script>

<style scoped>
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
div,
ul,
ol,
li,
dl,
dt,
dd,
h1 {
  margin: 0;
  padding: 0;
}
html {
  background: #ede7da;
}
li {
  list-style: none;
}
img {
  border: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}



.content {
  margin-top: 24px;
  position: relative;
  text-indent: 2em;
}




.article_page p {
  padding: 0 30px;
  white-space: nowrap;
  overflow: hidden;
}


.contentbuts {
  z-index: 1000;
  position: absolute;
  width: 100%;
  height: 100%;
}
</style>



