<script>
import qs from "qs";
import Vue from "vue";
import {Cell, Indicator} from "mint-ui";

Vue.component(Cell.name, Cell);
export default {
  name: "noveldetail",
  data() {
    return {
      like: false
    };
  },
  methods: {
    // 
    AddFavoriteBook(bookid) {
      var nowtime = new Date().getTime();
      var FavoriteBook = localStorage.getItem("FavoriteBook");
      if (FavoriteBook === null || undefined) {
        obj = {};
        obj[bookid] = {addtime: nowtime};
        localStorage.setItem("FavoriteBook", JSON.stringify(obj));
      } else {
        var obj = JSON.parse(FavoriteBook);
        obj[bookid] = {addtime: nowtime};
        localStorage.setItem("FavoriteBook", JSON.stringify(obj));
      }
    },

    DelFavoriteBook(bookid) {
      var FavoriteBook = localStorage.getItem("FavoriteBook");
      if (FavoriteBook === null || undefined) {
        obj = {};
        localStorage.setItem("FavoriteBook", JSON.stringify(obj));
      } else {
        var obj = JSON.parse(FavoriteBook);
        delete obj[bookid]
        localStorage.setItem("FavoriteBook", JSON.stringify(obj));
      }
    },
    markBook() {
      this.like = !this.like
      var markBookId = this.$store.state.bookid;
      if (this.like === true) {
        this.AddFavoriteBook(markBookId)
      } else {
        this.DelFavoriteBook(markBookId)
      }
    },
    // 
    // 
    checkMarkBook() {
      var markBookId = this.$store.state.bookid;
      var FavoriteBook = localStorage.getItem("FavoriteBook");
      if (FavoriteBook === null || undefined) {
      } else {
        var obj = JSON.parse(FavoriteBook);
        var k = obj[markBookId]
        if (k!==undefined){
          this.like=true
        }
        // console.log(k)
        // for (const key in obj) {
        //   console.log("like",key)
        //   if (key===markBookId){
        //     this.like=true
        //   }
        // }
      }
    }
    ,
    getSSBookClistId() {
      return eval(this.$store.state.noveldetail);
    },
    //  
    goBack() {
      this.$router.push({path: "/results"});
    },
    //  

    //bookid  
    getinfo() {
      var bookID = this.$store.state.bookid;

      var request = window.indexedDB.open("bookdb", 1);
      request.onsuccess = event => {
        var db = event.target.result;
        var transaction = db.transaction("book", "readwrite");
        var objectStore = transaction.objectStore("book");
        var index = objectStore.index("bookid");
        index.get(bookID).onsuccess = event => {
          this.$store.commit(
            "setnoveldetail",
            JSON.stringify(event.target.result["content"])
          );
        };
      };
    },
    //bookid  

    //  
    url2chapterlist() {
      Indicator.open('...');
      this.$router.push({
        path: "/chapterlist"
      });
    },
    //  

    // 
    downloadchapterlists() {
      var request = window.indexedDB.open("bookdb", 1);
      request.onsuccess = event => {
        var db = event.target.result;
        var transaction = db.transaction(["chapterlist"], "readwrite");
        var objectStore = transaction.objectStore("chapterlist");
        var index = objectStore.index("chapterurl");
        index.get(this.$store.state.bookid).onsuccess = event => {
          if (event.target.result === undefined) {
            console.log("");
            var SSBookClistId = JSON.parse(this.$store.state.noveldetail);
            console.log(SSBookClistId);
            let user = {
              SSBookClistId: SSBookClistId["Clist"][0]["SSBookClistId"]
            };
            this.$api.chapterlist(user)
              .then(response => {
                // response => url
                var response = response.data;
                if (response["status"] === 1) {
                  var chapterlist = response["data"];
                  this.$store.commit("setchapterlist", chapterlist);

                  var request = window.indexedDB.open("bookdb", 1);
                  request.onsuccess = event => {
                    var db = event.target.result;
                    var transaction = db.transaction(
                      ["chapterlist"],
                      "readwrite"
                    );
                    var objectStore = transaction.objectStore("chapterlist");
                    //   'allchapter'
                    objectStore.add({
                      chapterurl: this.$store.state.bookid,
                      content: chapterlist
                    });
                  };
                }
              })
              .catch(function (error) {
                console.log(error);
              });
          } else if (event.target.result != undefined) {
            console.log("");
            var chapterlist = event.target.result["content"];
            this.$store.commit("setchapterlist", chapterlist);
          }
        };
      };
    },
    // 

    //   
    readbook() {
      this.$router.push({
        path: "/reader"
      });
    }
    //   
  },
  computed: {
    details() {
      return eval(
        "(function(){return " + this.$store.state.noveldetail + ";})()"
      );
    }
  },
  beforeMount() {
    this.getinfo();
    this.checkMarkBook();
  },
  mounted() {
    setTimeout(() => Indicator.close(), 0)
    this.$store.commit("updaterouter", "detail");
    this.downloadchapterlists();
  },
  updated() {
    setTimeout(() => Indicator.close(), 0)
  },
  beforeDestroy() {
    console.log('noveldetail destroy OK');
  }
};
</script>

