  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.topback {
  position: fixed;
  z-index: 1000;
  background-color: #86bfffd9;
  border-radius: 10px;
  margin: 8px;
  border: 1px solid;
}

.ndtop {
