}

.cavrow {
  text-align: center;
  height: 3rem;
  line-height: 3rem;
  background-color: #f8f9fa;
  border-top: 1px solid;
  border-top-color: #c3c3c357;
}

.container {
  background-color: rgba(248, 248, 255, 0.1);
}

span {
  color: black;
}

.col-11 {
  margin-top: 9px;
}

span i {
  font-weight: bold;
}
</style>



