<template>
  <div class="fixed-top">
    <div class="alert alert-light" role="alert"></div>

    <div style="margin: 5em auto 2em;text-align: center">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>

    <div class="container-fluid">
      <div class="row justify-content-center">
        <div class="input-group mb-3 col-10">
          <input
            v-model="bookname"
            type="text"
            class="form-control"
            placeholder=""
            aria-label="Recipient's username"
            aria-describedby="basic-addon2"
            name="bookname"
          />
          <div class="input-group-append">
            <span @click="searchbut" class="input-group-text" id="basic-addon2"></span>
          </div>
        </div>
      </div>

      <bot></bot>
    </div>
  </div>
</template>

<script>
import bot from "./common/bot";
import {Indicator, Toast} from 'mint-ui';

export default {
  name: "index",
  components: {
    bot: bot
  },
  data() {
    return {
      bookname: ""
    };
  },
  methods: {
    searchbut() {
      Indicator.open('...');
      // 

      // bookname  
      let user = {
        bookname: this.bookname
      };
      try {
        this.$api.search(user)
          .then(response => {
            var response = response.data;
            console.log(response);
            if (response["status"] === 1) {
              const books = response["data"];
              this.$store.commit("increment", JSON.stringify(books));
              //  
              const request = window.indexedDB.open("bookdb", 1);
              request.onsuccess = function (event) {
                const db = event.target.result;
                const transaction = db.transaction("book", "readwrite");
                const objectStore = transaction.objectStore("book");
                let i;
                for (i = 0; i < books.length; i++) {
                  objectStore.add({
                    bookid: books[i]["ID"],
                    content: books[i]
                  });
                }
              };
            } else {
              console.log("");
              Toast("...");
            }
            //  
          })
          .catch(function (error) {
            console.log(error);
          });
      } catch (err) {
        Toast("...");
      }
      // 
      const request = window.indexedDB.open("bookdb", 1);
      request.onsuccess = event => {
        let scontent = this.bookname
        const db = event.target.result;
        const index = db.transaction(['searchhistory'], 'readonly').objectStore('searchhistory').index('sContent');
        const request = index.get(scontent);
        request.onsuccess = function (e) {
          const result = e.target.result;
          if (result === undefined) {
            let d = new Date();
            let scontentDatetime = d.getTime()
            const request = db.transaction("searchhistory", 'readwrite').objectStore("searchhistory").add({
              sContent: scontent,
              time: scontentDatetime
            });
            request.onsuccess = function (event) {
              console.log('');
            };
          } else {
            // ...
          }
        }
      };
      // bookname  
      // /results 
      this.$router.push({path: "/results"});
      // /results 
    }
  },
  mounted() {
    //
    console.log(" ")
    this.$store.commit("updaterouter", "index");
    //
    // Indicator.close();
    setTimeout(() => Indicator.close(), 0)
    console.log(" ")
  },
  updated() {
    this.$store.commit("increment", "");
  },
  beforeCreate() {
    //
    console.log(" ")
    Indicator.open('...');
  }
};
</script>

<style scoped>
</style>



