<template>
  <div>
    <div v-html="chaptershow" :style="constyle"></div>
    <div v-show="ibooklisten"></div>
    <div></div>
  </div>
</template>

<script>
import $ from "webpack-zepto";
export default {
  name: "readerview",
  props: ["middleibook"],
  data() {
    return {
      // 
      chaptercontent: [],
      // 
      constyle: ""
    };
  },
  methods: {
    //   -  html css
    ibook(sourcecontent) {
      // klass
      var klass = (function() {
        function isObject(o) {
          return typeof o === "object";
        }

        function isFunction(f) {
          return typeof f === "function";
        }

        var createObject =
          !Object.create ||
          function(proto) {
            function Class() {}

            Class.prototype = proto;
            return new Class();
          };

        function extend(target, source) {
          for (var key in source) {
            if (source.hasOwnProperty(key)) {
              target[key] = source[key];
            }
          }

          return target;
        }

        function klass(Parent, classDefinition) {
          var _Parent = Parent,
            _classDefinition = classDefinition;

          if (isObject(_Parent)) {
            _classDefinition = _Parent;
            _Parent = undefined;
          }

          if (!isObject(_classDefinition)) {
            return function() {};
          }

          var init = _classDefinition.init;

          function Class() {
            if (_Parent && isFunction(_Parent)) {
              _Parent.apply(this, arguments);
            }
            if (isFunction(init)) {
              var initReturn = init.apply(this, arguments);
            }
            if (initReturn) {
              return initReturn;
            }
          }

          if (_Parent) {
            //Class.prototype = new _Parent();
            Class.prototype = createObject(_Parent.prototype);
            //Class.prototype = extend(Class.prototype, _Parent.prototype);
          }

          Class.prototype = extend(Class.prototype, _classDefinition);
          Class.prototype.constructor = Class;

          return Class;
        }

        return klass;
      })();
      // klass

      // charTool
      /**
       * 
       */
      function isEnglishAlphabet(character) {
        var result = false;
        var charCode = character.charCodeAt();
        if (
          (charCode >= 65 && charCode <= 90) ||
          (charCode >= 97 && charCode <= 122)
        ) {
          result = true;
        }
        return result;
      }
      /**
       * 
       */
      function isEndChar(character) {
        var endCharArr = [
          ",",
          ".",
          "?",
          "!",
          ";",
          ":",
          "'",
          '"',
          "]",
          "}",
          ")",
          ">",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          ""
        ];

        isEndChar = function(character) {
          var result = false;
          for (var i = 0, l = endCharArr.length; i < l; i++) {
            if (character === endCharArr[i]) {
              result = true;
              break;
            }
          }
          return result;
        };

        return isEndChar(character);
      }
      /**
       * HTML
       */
      function encodeHTML(content) {
        return content
          .replace(/&/g, "&amp;")
          .replace(/ /g, "&nbsp;")
          .replace(/</g, "&lt;")
          .replace(/>/g, "&gt;")
          .replace(/"/g, "&quot;")
          .replace(/'/g, "&#39;");
      }
      // charTool

      // ArticleModel
      /**/
      function splitArticle(article) {
        return (article || "").split("\n");
      }
      /**
       * 
       */
      function splitSection(section) {
        return (section || "").split("");
      }
      /**
       * @param fontStyleStatus
       * @param article
       * @returns {Array}
       * @constructor
       */
      function ArticleModel(fontStyleStatus, article) {
        var charWidth = fontStyleStatus.charWidth;

        var sectionArr = splitArticle(article); //
        var charArr; // 
        var articleModel = []; // 

        var chars = ""; // 
        var curChar = ""; // 
        var width = 0; // chars
        var isFullWord = true; // /

        for (var i = 0, sLen = sectionArr.length; i < sLen; i++) {
          charArr = splitSection(sectionArr[i]);
          articleModel.push([]);

          var j = 0;
          var wLen = charArr.length;
          while (j < wLen) {
            curChar = charArr[j];

            // 
            if (isEnglishAlphabet(curChar)) {
              // 
              isFullWord = false;
              chars += curChar;
              width += charWidth.get(curChar);
              j++;
            } else {
              isFullWord = true;
            }

            //  articleModel 
            if (isFullWord) {
              // chars
              if (!chars.length) {
                chars += curChar;
                width += charWidth.get(curChar);
                j++;
              }
              articleModel[i].push({
                width: width,
                character: chars
              });
              curChar = chars = "";
              width = 0;
            }
          }
        }
        charWidth.remove();
        return articleModel;
      }
      // ArticleModel

      // CharWidth
      var CharWidth = klass({
        init: function(style) {
          this.style = style;
          this.charMap = {}; // DOM
          this.$charWrap = this.createWrap();
        },

        createWrap: function() {
          var $charWrap = $(
            '<span style="opacity: 0; position: absolute; left: -9999px; top: -9999px;"></span>'
          );
          $charWrap.css($.extend({}, this.style));
          $("body").append($charWrap);
          return $charWrap;
        },

        get: function(character) {
          var key = character.charCodeAt();
          // charMapDOM
          if (!this.charMap[key] || this.charMap[key].width == undefined) {
            if (!this.$charWrap) {
              this.$charWrap = this.createWrap();
            }
            this.$charWrap.html(encodeHTML(character));
            this.charMap[key] = {
              character: character,
              width: this.$charWrap.width()
            };
          }
          return this.charMap[key].width;
        },

        clear: function() {
          this.$charWrap.html("");
        },

        remove: function() {
          this.$charWrap && this.$charWrap.remove();
          this.$charWrap = null;
        }
      });
      // CharWidth

      // FontStyleController
      var FontStyleController = klass({
        init: function(option) {
          this.option = option || {
            small: {
              fontSize: "12px",
              lineHeight: "20px",
              letterSpacing: "0px"
            },
            normal: {
              fontSize: "16px",
              lineHeight: "24px",
              letterSpacing: "1px"
            },
            big: {
              fontSize: "20px",
              lineHeight: "28px",
              letterSpacing: "0px"
            }
          };
          this.currentOptionToken = undefined;
          this.charWidth = {};
        },

        set: function(optionToken) {
          var opt = this.option[optionToken];
          var charW = this.charWidth[optionToken];
          if (opt) {
            if (!charW) {
              this.charWidth[optionToken] = new CharWidth(opt);
            }
            this.currentOptionToken = optionToken;
          }
        },

        getStatus: function() {
          var cur = this.currentOptionToken;
          if (!cur) {
            return {
              error: 'Please set the "optionToken".'
            };
          }
          return {
            style: this.option[cur],
            charWidth: this.charWidth[cur]
          };
        }
      });
      // FontStyleController

      // renderPage
      function wrapPage(content, pageClass) {
        return (
          '<div style="display: unset" ' +
          (pageClass ? "class=" + pageClass : "") +
          ">" +
          content +
          "</div>"
        );
      }
      function wrapChar(content, key) {
        return '<span data-key="' + key + '">' + content + "</span>";
      }
      function renderPage(config) {
        var blank = config.blank || [30, 30, 30, 30]; // ,,,
        var article = config.article;
        var pageClass = config.pageClass;
        var fontStyleStatus = config.fontStyleStatus;

        var articleModel = new ArticleModel(fontStyleStatus, article);
        var style = fontStyleStatus.style;
        var letterSpacing = style.letterSpacing
          ? +style.letterSpacing.replace(/px$/, "")
          : 0;
        var $window = $(window);
        var pageW = $window.width() - blank[1] - blank[3];
        var pageH = $window.height() - blank[0] - blank[2];
        var maxLineOfPage = Math.floor(
          pageH / +style.lineHeight.replace(/px$/, "")
        );
        var pageLength = 0; // 
        var lineWidth = 0; // 
        var articleStr = ""; // 
        var articleList = []; //
        var pageStr = ""; // 
        var lineStr = ""; // 
        var lineCharCounter = 0; // 
        var spaceWidth = 0; // 
        var curChar = ""; // 
        var wordNum = 0; // 

        for (var i = 0, l1 = articleModel.length; i < l1; i++) {
          var j = 0,
            l2 = articleModel[i].length;

          while (j < l2) {
            curChar = articleModel[i][j].character;
            if (pageLength < maxLineOfPage) {
              lineWidth += articleModel[i][j].width;
              if (lineWidth <= pageW) {
                // 
                //lineStr += '<span data-key="'+ j +'">' + curChar + '</span>';
                lineStr += wrapChar(curChar, wordNum++);
                lineCharCounter += curChar.length;
                j++;
              } else {
                // 
                // 
                spaceWidth = pageW - lineWidth + articleModel[i][j].width;
                if (isEndChar(curChar)) {
                  //lineStr += '<span data-key="'+ j +'">' + curChar + '</span>';
                  lineStr += wrapChar(curChar, wordNum++);
                  spaceWidth = spaceWidth - articleModel[i][j].width;
                  lineCharCounter += curChar.length;
                  j++;
                }

                if (Math.abs(spaceWidth) > 1) {
                  pageStr +=
                    '<p style="margin-bottom:0;letter-spacing: ' +
                    (letterSpacing + spaceWidth / lineCharCounter) +
                    'px;">' +
                    lineStr +
                    "</p>";
                } else {
                  pageStr += '<p style="margin-bottom: 0;">' + lineStr + "</p>";
                }
                pageLength++;
                // 
                lineStr = "";
                lineWidth = 0;
                lineCharCounter = 0;
              }
            } else {
              // 
              pageStr = wrapPage(pageStr, pageClass);
              articleList.push(pageStr);
              articleStr += pageStr;
              // 
              pageStr = "";
              pageLength = 0;
            }
          }
          // 
          if (lineStr.length > 0) {
            // 
            pageStr += '<p style="margin-bottom: 0;">' + lineStr + "</p>";
            pageLength++;
            // 
            lineStr = "";
            lineWidth = 0;
            lineCharCounter = 0;
          }
        }
        // 
        pageStr = wrapPage(pageStr, pageClass);
        articleList.push(pageStr);
        articleStr += pageStr;
        // 
        pageStr = "";
        pageLength = 0;
        //
        // return articleStr;
        return articleList;
      }
      // renderPage

      // 
      // var $article = $('#article');
      var fontStyleController = new FontStyleController();

      // small big  normal
      fontStyleController.set(this.$store.state.fontsize);
      // htmlStr html
      var htmlStr = renderPage({
        article: sourcecontent,
        pageClass: "articlecls",
        fontStyleStatus: fontStyleController.getStatus()
      });
      this.constyle = fontStyleController.getStatus().style;
      this.chaptercontent = htmlStr;
    }
  },
  computed: {
    ibooklisten() {
      try {
        this.ibook(this.middleibook);
      } catch (error) {
        this.ibook(this.middleibook);
      }
      return false;
    },
    chaptershow() {
      var index = this.$store.state.showindex;
      if (index === -1) {
        return this.chapterlists[this.chapterlists.length - 1];
      } else {
        return this.chaptercontent[this.$store.state.showindex];
      }
    }
  },
  watch: {
    chaptercontent: function(val) {
      this.$store.commit("setmaxindex", val.length);
    }
  }
};
</script>

<style>
div.articlecls {
  display: unset;
}
</style>


