<template>
      <div class="fixed-top Lname" style="z-index:0;">
      <p class="font-weight-light col-8 web-font">{{lname}}</p>
    </div>
</template>

<script>
export default {
props:['lname']
}
</script>

<style>
.Lname p{
    font-size: .75rem;
    font-weight: 400;
    position: absolute;
    top: 7px;
    left: 2rem;
    color: rgba(0, 0, 0, 0.51);
    z-index: 0;
}

@font-face {
  font-family: 'webfont';
  font-display: swap;
  src: url('//at.alicdn.com/t/webfont_liql36r5e.eot'); /* IE9*/
  src: url('//at.alicdn.com/t/webfont_liql36r5e.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('//at.alicdn.com/t/webfont_liql36r5e.woff2') format('woff2'),
  url('//at.alicdn.com/t/webfont_liql36r5e.woff') format('woff'), /* chromefirefox */
  url('//at.alicdn.com/t/webfont_liql36r5e.ttf') format('truetype'), /* chromefirefoxoperaSafari, Android, iOS 4.2+*/
  url('//at.alicdn.com/t/webfont_liql36r5e.svg#AlibabaPuHuiTiL') format('svg'); /* iOS 4.1- */
}

.web-font{
    font-family:"webfont" !important;
    font-size:16px;font-style:normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
}
</style>


