<template>
  <div>
    <div :class="Dwatch"></div>
    <div :class="HistoryStore"></div>
  </div>
</template>

<script>
import qs from "qs";
import Vue from "vue";
export default {
  methods: {
    downloadNextchapter(Lurl) {
      try {
        var request = window.indexedDB.open("bookdb", 1);
        request.onsuccess = event => {
          var db = event.target.result;
          var transaction = db.transaction(["chapterlist"], "readwrite");
          var objectStore = transaction.objectStore("chapterlist");
          var index = objectStore.index("chapterurl");

          index.get(Lurl).onsuccess = event => {
            if (event.target.result === undefined) {
              let user = {
                Lurl: Lurl
              };
              this.$api.chapter(user)
                .then(response => {
                  var responseresult = response.data;
                  var request = window.indexedDB.open("bookdb", 1);
                  request.onsuccess = event => {
                    var db = event.target.result;
                    var transaction = db.transaction(
                      "chapterlist",
                      "readwrite"
                    );
                    var objectStore = transaction.objectStore("chapterlist");
                    //   'url'
                    objectStore.add({
                      chapterurl: Lurl,
                      content: responseresult
                    });
                  };
                })
                .catch(function(error) {
                  console.log(error);
                });
            }
            if (event.target.result !== undefined) {
            }
          };
        };
      } catch (err) {
        console.log(err);
      }
    },
    gettime() {
      var nowtime = new Date().getTime();
      return nowtime;
    },
    updateHS(bookid, datas) {
      var HSS = localStorage.getItem("HS");
      if (HSS === null || undefined) {
        obj = {};
        obj[bookid] = datas;
        localStorage.setItem("HS", JSON.stringify(obj));
      } else {
        var obj = JSON.parse(HSS);
        obj[bookid] = datas;
        localStorage.setItem("HS", JSON.stringify(obj));

      }
    }
  },
  computed: {
    Dwatch() {
      var chapterlist = eval(this.$store.state.chapterlists);
      var chapterindex = this.$store.state.chapterindex;
      if (chapterlist[chapterindex + 1]) {
        console.log("");
        try {
          this.downloadNextchapter(chapterlist[chapterindex + 1]["Lurl"]);
        } catch (err) {}
      }
      return "Dwatch core";
    },
    HistoryStore() {
      var HSbookid = this.$store.state.bookid;
      var HSchapterindex = this.$store.state.chapterindex;
      var HSshowindex = this.$store.state.showindex;
      var HSD = {
        chapterindex: HSchapterindex,
        showindex: HSshowindex,
        lasttime: this.gettime()
      };
      this.updateHS(HSbookid, HSD);
      return "HistoryStore"
    }
  },
  // mounted() {
  //   var HSbookid = this.$store.state.bookid;
  //   var HSchapterindex = this.$store.state.chapterindex;
  //   var HSshowindex = this.$store.state.showindex;
  //   var HSD = {
  //     chapterindex: HSchapterindex,
  //     showindex: HSshowindex,
  //     lasttime: this.gettime()
  //   };
  //   this.updateHS(HSbookid, HSD);
  //   console.log("Download C ")
  // }
};
</script>

