<template>
  <transition name="el-zoom-in-top" id="tophidden">
    <div v-show="menu" class="transition-box fixed-top hiddentop" @click="goback">
      <svg
        t="1568997877815"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="2882"
        width="32"
        height="32"
      >
        <path
          d="M694.272 809.024l-295.808-286.848 295.36-274.752a32 32 0 0 0-43.616-46.848l-320 297.696a32 32 0 0 0-0.512 46.4l320 310.304a32.032 32.032 0 0 0 44.576-45.952"
          p-id="2883"
        />
      </svg>
    </div>
  </transition>
</template>

<script>
export default {
  methods: {
    //
    goback() {
      this.$router.push({ path: "/results" });
    }
  },
  computed: {
    menu() {
      return this.$store.state.reader.menushow;
    }
  }
};
</script>

<style>
/*   */
.hiddentop {
  z-index: 1000;
  background-color: #f8f9fa;
  border-bottom: 1px solid;
  border-bottom-color: #c3c3c357;
}
.hiddentop i {
  margin-left: 11px;
  font-size: x-large;
  color: #020202;
}
/*   */
</style>


