// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import '../node_modules/bootstrap/dist/css/bootstrap.css'
import '../node_modules/bootstrap/dist/js/bootstrap'


import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import 'mint-ui/lib/style.css'


import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(VueAxios, axios);

import api from '@/utils/ajax/api'

// Vuex
import store from "./store"

Vue.use(Vuex);


new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>',
  render: h => h(App)
});


Vue.config.productionTip = false;
Vue.use(ElementUI);


//indexedBD
const request = window.indexedDB.open('bookdb', 1);
request.onupgradeneeded = function (event) {
  const db = event.target.result;

  // 
  const objectStore = db.createObjectStore('book', {keyPath: 'id', autoIncrement: true});
  objectStore.createIndex('bookid', 'bookid', {unique: true});
  // 
  const chapterlistStore = db.createObjectStore('chapterlist', {keyPath: 'chapterurl', autoIncrement: true});
  chapterlistStore.createIndex('chapterurl', 'chapterurl', {unique: true});
  // 
  const searchHistoryStore = db.createObjectStore('searchhistory', {keyPath: 'id', autoIncrement: true});
  searchHistoryStore.createIndex('sContent', 'sContent', {unique: true});
};

request.onerror = function (event) {
  alert("Why didn't you allow my web app to use IndexedDB?!");
};


Vue.prototype.$api = api;










