<template>
  <div>
    <nav class="navbar sticky-top navbar-light bg-light justify-content-center">
    </nav>

    <div class="container-fluid" v-if="results">
      <div class="list-group list-group-flush" v-for="(result,key) in results" :key="key">
        <a
          class="list-group-item list-group-item-action flex-column align-items-start"
          @click.prevent="url2noveldetail(result['ID'])"
        >
          <div class="row">
            <div class="col-4 resultCol4">
              <img
                src="https://cdn.static.17k.com/book/189x272/64/64/3006464.jpg-189x272?v=1564153041000"
                alt
              />
            </div>
            <div class="col-8 resultCol8">
              <div class="d-flex w-100 justify-content-between">
                <h6 class="mb-1">{{ result.Name }}</h6>
                <small class="text-muted">
                  <a :href="result['Clist'][0]['Source']"></a>
                </small>
              </div>
              <h6
                class="card-subtitle mb-2 text-muted h6info"
              >{{ result.Author }} - {{ result.BookStatus }}</h6>
              <div v-for="cn in result.CName" :key="cn">
                <h6 class="card-subtitle mb-2 text-muted h6info">{{ cn }}</h6>
              </div>
              <p
                class="mb-1"
              >{{ result.Desc }}</p>
            </div>
          </div>

        </a>
      </div>
      <div class="botblock"></div>
    </div>
    <div class="container-fluid" v-if="showNotFund">
      <div class="row">
        <div class="col-2"></div>
        <div class="col-8" :style="{'margin-top': this.getClientWidth + 'px'}"> 
          <br>
          
        </div>
        <div class="col-2"></div>
      </div>

    </div>
    <bot></bot>
  </div>
</template>

<script>
import {Toast} from "mint-ui";
import bot from "./common/bot";
import {Indicator} from 'mint-ui';

export default {
  name: "SearchResults",
  components: {
    bot: bot
  },
  data() {
    return {
      bookname: "",
      showNotFund: false,
    };
  },
  methods: {
    // noveldetail 
    url2noveldetail(bookid) {
      Indicator.open('...');
      this.$store.commit("setbookid", bookid);
      console.log(bookid)
      this.$router.push({
        path: "/detail",
        query: {
          bookid: bookid,
          frompath: "searchresult"
        }
      });
    },
    // noveldetail 

  },
  computed: {
    //  
    results() {
      if (this.$store.state.bookresults) {
        var bookresultslist = eval(
          "(function(){return " + this.$store.state.bookresults + ";})()"
        )
        if (bookresultslist.length !== 0) {
          return bookresultslist;
        } else {
          return undefined
        }
      } else {
        return undefined
      }
    },
    //  
    getClientWidth() {
      return document.documentElement.clientWidth / 2;
    },

  },
  mounted() {
    // setTimeout(() => Indicator.close(), 0)
    this.$store.commit("updaterouter", "results");
  },
  beforeUpdate() {
    Indicator.open('...');
  },
  updated() {
    if (this.results === undefined) {
      this.showNotFund = true
    }
    setTimeout(() => Indicator.close(), 0)
  }
};
</script>

<style scoped>
.resultCol4 {
  text-align: center;
  padding: 0;
}

.resultCol4 img {
  margin: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.resultCol8 {
  padding-right: 0;
}

.resultCol8 p {
  height: 72px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.h6info {
  margin-top: 0em;
}

small {
  width: 9em;
}


.botblock {
  height: 50px;
  width: 100%;
}
</style>



