import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

// login 
import reader from "./components/reader/store/store"

export default new Vuex.Store({
  // moduleslogin 
  state: {
    hosturl: '',
    // hosturl: document.location,

    bookresults: "", //
    noveldetail: "", //

    // 
    lasturl: "",
    nowurl: "",
    // 

    loading: false,

    //id url 
    bookid: "",
    chapterlists: undefined,
    chapterindex: 0,
    lastmaxindex: undefined,
    fontsize: "normal",
    showindex: 0,
    maxindex: undefined,

    //  
    bookstore: {}
  },
  getters: {
    chapterlistnum: function (state) {
      if (state.chapterlists) {
        var list = eval(state.chapterlists);
        return list.length
      }
    }
  },
  mutations: {
    setmaxindex(state, n) {
      state.maxindex = n
    },
    setfontsize(state, n) {
      state.fontsize = n
    },
    setlastmaxindex(state, n) {
      state.lastmaxindex = n
    },
    setshowindex(state, n) {
      state.showindex = n
    },

    increment(state, n) {
      state.bookresults = "";
      state.bookresults += n;
    },
    //  index   searchresults  noveldetail chapterlist  reader
    updaterouter(state, n) {
      state.lasturl = state.nowurl;
      state.nowurl = n
    },
    setbookid(state, n) {
      state.bookid = n
    },
    setnoveldetail(state, n) {
      state.noveldetail = n
    },
    setchapterlist(state, n) {
      state.chapterlists = n
    },
    setchapterindex(state, n) {
      state.chapterindex = n
    },
  },
  modules: {
    reader: reader
  }
})



