<template>
  <div>
    <!--    -->
    <div
      class="title fixed-top"
    >
      <div class="row">
        <div class="col-6 text-center" @click="showMenu='ydli'"></div>
        <div class="col-6 text-center" @click="showMenu='sc'"></div>
      </div>
    </div>
    <!--    -->

    <!--    -->
    <div class="sc" v-show="showMenu==='sc'">
      <div class="none text-center" v-show="sortByTimeForFavrite.length===0">
        
      </div>
      <div
        class="book"
        v-for="(book,key) in sortByTimeForFavrite"
        :key="key"
      >
        <div
          class="col-3 bookicon"
          @click="deleteBookFav(book.name,book.bookid)"
        >
          <svg
            t="1569611221276"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="7916"
            data-spm-anchor-id="a313x.7781069.0.i46"
            width="48"
            height="48"
          >
            <path
              d="M742.4 921.6l-512 0c-14.1312 0-25.6-11.4688-25.6-25.6s11.4688-25.6 25.6-25.6l512 0c14.1312 0 25.6 11.4688 25.6 25.6s-11.4688 25.6-25.6 25.6z"
              fill="#63f7de"
              p-id="7917"
              data-spm-anchor-id="a313x.7781069.0.i45"
              class
            />
            <path
              d="M844.8 153.6c-14.1312 0-25.6 11.4688-25.6 25.6l0 768c0 14.1312-11.4688 25.6-25.6 25.6l-563.2 0c-42.3424 0-76.8-34.4576-76.8-76.8s34.4576-76.8 76.8-76.8l460.8 0c42.3424 0 76.8-34.4576 76.8-76.8l0-614.4c0-42.3424-34.4576-76.8-76.8-76.8l-512 0c-42.3424 0-76.8 34.4576-76.8 76.8l0 768c0 70.5536 57.4464 128 128 128l563.2 0c42.3424 0 76.8-34.4576 76.8-76.8l0-768c0-14.1312-11.4688-25.6-25.6-25.6zM179.2 102.4l512 0c14.1312 0 25.6 11.4688 25.6 25.6l0 614.4c0 14.1312-11.4688 25.6-25.6 25.6l-460.8 0c-28.7744 0-55.3984 9.5744-76.8 25.6512l0-665.6512c0-14.1312 11.4688-25.6 25.6-25.6z"
              fill="#437dff"
              p-id="7918"
              data-spm-anchor-id="a313x.7781069.0.i42"
              class
            />
          </svg>
        </div>
        <!--        TODO:-->
        <div
          class="col-9 bookclick"
          @click=""
        >
          <div class="row">{{ book.name }}-{{ book.author }}</div>
          <div class="row">: {{ Number(book.addtime) | formatDate }}</div>
        </div>
      </div>
    </div>
    <!--    -->

    <!--    -->
    <div class="ydli" v-show="showMenu==='ydli'">
      <div class="none text-center" v-show="sortBytimeForHS.length === 0">
        
      </div>
      <div
        class="book"
        v-for="(book,key) in sortBytimeForHS"
        :key="key"
      >
        <div
          class="col-3 bookicon"
          @click="deletebookHS(book.name,book.bookid)"
        >
          <svg
            t="1569611221276"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="7916"
            data-spm-anchor-id="a313x.7781069.0.i46"
            width="48"
            height="48"
          >
            <path
              d="M742.4 921.6l-512 0c-14.1312 0-25.6-11.4688-25.6-25.6s11.4688-25.6 25.6-25.6l512 0c14.1312 0 25.6 11.4688 25.6 25.6s-11.4688 25.6-25.6 25.6z"
              fill="#63f7de"
              p-id="7917"
              data-spm-anchor-id="a313x.7781069.0.i45"
              class
            />
            <path
              d="M844.8 153.6c-14.1312 0-25.6 11.4688-25.6 25.6l0 768c0 14.1312-11.4688 25.6-25.6 25.6l-563.2 0c-42.3424 0-76.8-34.4576-76.8-76.8s34.4576-76.8 76.8-76.8l460.8 0c42.3424 0 76.8-34.4576 76.8-76.8l0-614.4c0-42.3424-34.4576-76.8-76.8-76.8l-512 0c-42.3424 0-76.8 34.4576-76.8 76.8l0 768c0 70.5536 57.4464 128 128 128l563.2 0c42.3424 0 76.8-34.4576 76.8-76.8l0-768c0-14.1312-11.4688-25.6-25.6-25.6zM179.2 102.4l512 0c14.1312 0 25.6 11.4688 25.6 25.6l0 614.4c0 14.1312-11.4688 25.6-25.6 25.6l-460.8 0c-28.7744 0-55.3984 9.5744-76.8 25.6512l0-665.6512c0-14.1312 11.4688-25.6 25.6-25.6z"
              fill="#437dff"
              p-id="7918"
              data-spm-anchor-id="a313x.7781069.0.i42"
              class
            />
          </svg>
        </div>
        <div
          class="col-9 bookclick"
          @click="readbook(book.bookid,book.chapterindex,book.showindex)"
        >
          <div class="row">{{ book.name }}-{{ book.author }}</div>
          <div class="row">: {{ Number(book.lasttime) | formatDate }}</div>
        </div>
      </div>
    </div>
    <!--    -->

    <bot></bot>
  </div>
</template>

<script>
import bot from "./common/bot";
import {formatdate} from "./Lib/data";
import {MessageBox} from "mint-ui";


export default {
  name: "bookshelf",
  components: {
    bot: bot
  },
  data() {
    return {
      showMenu: "ydli",
      HSdata: [],
      FavoriteBook: [],
    };
  },
  methods: {
    getHS() {
      var HSS = localStorage.getItem("HS");
      var books = JSON.parse(HSS);
      for (const bookid in books) {
        if (books.hasOwnProperty(bookid)) {
          const element = books[bookid];
          var request = window.indexedDB.open("bookdb", 1);
          request.onsuccess = event => {
            var db = event.target.result;
            var transaction = db.transaction("book", "readwrite");
            var objectStore = transaction.objectStore("book");
            var index = objectStore.index("bookid");
            index.get(Number(bookid)).onsuccess = event => {
              try {
                var bookobj = event.target.result.content;
              } catch (err) {
              }

              if (bookobj !== undefined) {
                this.HSdata.push({
                  name: bookobj.Name,
                  author: bookobj.Author,
                  lasttime: element.lasttime,
                  chapterindex: element.chapterindex,
                  showindex: element.showindex,
                  bookid: bookid
                });
              }
            };
          };
        }
      }
    },
    getFavoriteBooks() {
      var FavoriteBook = localStorage.getItem("FavoriteBook");
      var books = JSON.parse(FavoriteBook);
      for (const bookid in books) {
        if (books.hasOwnProperty(bookid)) {
          const element = books[bookid];
          var request = window.indexedDB.open("bookdb", 1);
          request.onsuccess = event => {
            var db = event.target.result;
            var transaction = db.transaction("book", "readwrite");
            var objectStore = transaction.objectStore("book");
            var index = objectStore.index("bookid");
            index.get(Number(bookid)).onsuccess = event => {
              try {
                var bookobj = event.target.result.content;
              } catch (err) {
              }

              if (bookobj !== undefined) {
                this.FavoriteBook.push({
                  name: bookobj.Name,
                  author: bookobj.Author,
                  addtime: element.addtime,
                  bookid: bookid
                });
              }
            };
          };
        }
      }
    },
    readbook(bookid, chapterindex, showindex) {
      var request = window.indexedDB.open("bookdb", 1);
      request.onsuccess = event => {
        var db = event.target.result;
        db
          .transaction("chapterlist", "readwrite")
          .objectStore("chapterlist")
          .index("chapterurl")
          .get(Number(bookid)).onsuccess = event => {
          this.$store.commit("setchapterlist", event.target.result.content);
          this.$store.commit("setchapterindex", chapterindex);
          this.$store.commit("setshowindex", showindex);
          this.$router.push({path: "/reader"});
        };
      };
    },
    deletebookHS(name, bookid) {
      MessageBox.confirm(" " + name + "?").then(action => {
        var request = window.indexedDB.open("bookdb", 1);
        request.onsuccess = event => {
          var db = event.target.result;
          db
            .transaction("chapterlist", "readwrite")
            .objectStore("chapterlist")
            .index("chapterurl")
            .get(Number(bookid)).onsuccess = event => {
            var chptlist = eval(event.target.result.content);
            for (let index = 0; index < chptlist.length; index++) {
              const element = chptlist[index];
              db
                .transaction("chapterlist", "readwrite")
                .objectStore("chapterlist")
                .delete(element["Lurl"]).onsuccess = event => {
              };
            }
            var HSS = localStorage.getItem("HS");
            var obj = JSON.parse(HSS);
            delete obj[bookid]
            localStorage.setItem("HS", JSON.stringify(obj));
            this.HSdata = []
            this.getHS();
          };
        };
        console.log("deleted book");
      });
    },
    deleteBookFav(name,bookid) {

      MessageBox.confirm(" " + name + "?").then(action => {
          var FavoriteBook = localStorage.getItem("FavoriteBook");
          if (FavoriteBook === null || undefined) {
            obj = {};
            localStorage.setItem("FavoriteBook", JSON.stringify(obj));
          } else {
            var obj = JSON.parse(FavoriteBook);
            delete obj[bookid]
            localStorage.setItem("FavoriteBook", JSON.stringify(obj));
            //
            this.FavoriteBook = []
            this.getFavoriteBooks()
          }
        }
      )


    }
    ,
    sortByTime(array, key) {
      return array.sort(function (a, b) {
        var x = a[key];
        var y = b[key];
        return x > y ? -1 : x < y ? 1 : 0;
      });
    }
  },
  computed: {
    sortBytimeForHS() {
      return this.sortByTime(this.HSdata, "lasttime");
    },
    sortByTimeForFavrite() {
      return this.sortByTime(this.FavoriteBook, "addtime");
    }
  },
  filters: {
    formatDate(time) {
      var date = new Date(time);
      return formatdate(date, "yyyy-MM-dd hh:mm");
    }
  },
  beforeMount() {
    this.getHS();
    this.getFavoriteBooks();
  }
};


</script>

<style>
@font-face {
  font-family: "webfont";
  font-display: swap;
  src: url("//at.alicdn.com/t/webfont_fkiy8n8deqr.eot"); /* IE9*/
  src: url("//at.alicdn.com/t/webfont_fkiy8n8deqr.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("//at.alicdn.com/t/webfont_fkiy8n8deqr.woff2") format("woff2"),
  url("//at.alicdn.com/t/webfont_fkiy8n8deqr.woff") format("woff"),
    /* chromefirefox */ url("//at.alicdn.com/t/webfont_fkiy8n8deqr.ttf") format("truetype"),
    /* chromefirefoxoperaSafari, Android, iOS 4.2+*/ url("//at.alicdn.com/t/webfont_fkiy8n8deqr.svg#-") format("svg"); /* iOS 4.1- */
}

.web-font {
  font-family: "webfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

.fixed-top {
  text-align: center;
  margin-top: 5px;
  border-bottom: 1px solid;
  padding-bottom: 5px;
  border-color: rgb(195, 195, 195);
}

.ydli {
  margin-top: 50px;
}

.sc {
  margin-top: 50px;
}

.none{
  margin-top: 7rem;
}

.book {
  display: flex;
  border-bottom: 1px solid;
  padding: 7px;
  border-color: rgba(195, 195, 195, 0.34);
}

.bookicon {
  text-align: center;
  margin-right: -1rem;
}

.bookclick {
  margin-left: 1rem;
}
</style>



