<template>
  <!-- <i class="web-font"> {{nowtime}} </i> -->
      <div class="time fixed-bottom row" style="z-index:0;">
        <i class="col web-font" id="index">{{ ComShowindex + 1}} / {{getmaxindex}}</i>
        <i class="col web-font" id="time"> {{nowtime}}</i>

    </div>
</template>

<script>
  export default {
    name: "vuetime",
    data(){
      return {
        nowtime: new Date().toTimeString().slice(0, 5)
        }
    },
    computed: {
          // 
    ComShowindex() {
      return this.$store.state.showindex;
    },
    // 
    getmaxindex() {
      return this.$store.state.maxindex;
    }
    },
    mounted(){
            // 
            let _this = this;
            this.timer = setInterval(function () {
                _this.nowtime = new Date().toTimeString().slice(0, 5)
            });
            // 
    },
    beforeDestroy(){
                  // 
            if (this.timer) {
                clearInterval(this.timer);
            }
    }
  }
</script>

<style scoped>
