/*  url('//at.alicdn.com/t/webfont_mn33s34kmqm.woff') format('woff'), !* chromefirefox *!*/
/*  url('//at.alicdn.com/t/webfont_mn33s34kmqm.ttf') format('truetype'), !* chromefirefoxoperaSafari, Android, iOS 4.2+*!*/
/*  url('//at.alicdn.com/t/webfont_mn33s34kmqm.svg#AlibabaPuHuiTiL') format('svg'); !* iOS 4.1- *!*/
/*}*/

/*.web-font{*/
/*    font-family:"webfont" !important;*/
/*    font-size:16px;font-style:normal;*/
/*    -webkit-font-smoothing: antialiased;*/
/*    -webkit-text-stroke-width: 0.2px;*/
/*    -moz-osx-font-smoothing: grayscale;*/
/*}*/

.time #index{
    margin-left: 2rem;
}
.time #time{
    text-align: right;
    margin-right: 2rem;
}
</style>



